'use client';

import { useState, useEffect } from 'react';
import { mockVehicles } from '@/data/mockData';
import { Device } from '@/types';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import {
  Wrench,
  Search,
  Filter,
  AlertTriangle,
  Calendar,
  DollarSign,
  MapPin
} from 'lucide-react';
import Link from 'next/link';

export default function EquipmentListPage() {
  const [searchTerm, setSearchTerm] = useState('');
  const [filterType, setFilterType] = useState<string>('all');
  const [filterStatus, setFilterStatus] = useState<string>('all');
  const [filterCriticality, setFilterCriticality] = useState<string>('all');
  const [now, setNow] = useState<Date | null>(null);

  useEffect(() => {
    // eslint-disable-next-line react-hooks/set-state-in-effect
    setNow(new Date());
  }, []);

  // Collect all equipment from all vehicles
  const allEquipment: (Device & { vehicleName: string; vehicleId: string })[] = [];
  mockVehicles.forEach(vehicle => {
    vehicle.devices.forEach(device => {
      allEquipment.push({
        ...device,
        vehicleName: vehicle.name,
        vehicleId: vehicle.id
      });
    });
  });

  // Filter equipment
  const filteredEquipment = allEquipment.filter(equipment => {
    const matchesSearch = equipment.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      equipment.type.toLowerCase().includes(searchTerm.toLowerCase()) ||
      equipment.vehicleName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      (equipment.serialNumber && equipment.serialNumber.toLowerCase().includes(searchTerm.toLowerCase()));

    const matchesType = filterType === 'all' || equipment.type === filterType;
    const matchesStatus = filterStatus === 'all' || equipment.status === filterStatus;
    const matchesCriticality = filterCriticality === 'all' || equipment.criticality === filterCriticality;

    return matchesSearch && matchesType && matchesStatus && matchesCriticality;
  });

  // Get unique types
  const uniqueTypes = Array.from(new Set(allEquipment.map(e => e.type)));

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'overdue': return <Badge variant="destructive">Overdue</Badge>;
      case 'due': return <Badge variant="warning">Due Soon</Badge>;
      case 'upcoming': return <Badge variant="default">Upcoming</Badge>;
      case 'completed': return <Badge variant="success">Up to Date</Badge>;
      default: return <Badge variant="secondary">{status}</Badge>;
    }
  };

  const getCriticalityBadge = (criticality: string) => {
    switch (criticality) {
      case 'critical': return <Badge variant="destructive">Critical</Badge>;
      case 'high': return <Badge variant="warning">High</Badge>;
      case 'medium': return <Badge variant="default">Medium</Badge>;
      case 'low': return <Badge variant="secondary">Low</Badge>;
      default: return <Badge variant="secondary">{criticality}</Badge>;
    }
  };

  const estimateCost = (criticality: string) => {
    switch (criticality) {
      case 'critical': return 1500;
      case 'high': return 1000;
      case 'medium': return 600;
      default: return 300;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Equipment Inventory</h1>
          <p className="mt-1 text-sm text-gray-600">
            Complete list of all equipment across all vehicles - {allEquipment.length} total items
          </p>
        </div>

        {/* Statistics */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Total Equipment</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-gray-900">{allEquipment.length}</div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Overdue Maintenance</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-600">
                {allEquipment.filter(e => e.status === 'overdue').length}
              </div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Due Soon</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-yellow-600">
                {allEquipment.filter(e => e.status === 'due').length}
              </div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Critical Priority</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-purple-600">
                {allEquipment.filter(e => e.criticality === 'critical').length}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Filters */}
        <Card className="mb-8">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Filter className="h-5 w-5" />
              Filter Equipment
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              <div>
                <label htmlFor="search" className="block text-sm font-medium text-gray-700 mb-1">
                  Search
                </label>
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <input
                    type="text"
                    id="search"
                    placeholder="Search equipment..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                  />
                </div>
              </div>

              <div>
                <label htmlFor="filterType" className="block text-sm font-medium text-gray-700 mb-1">
                  Equipment Type
                </label>
                <select
                  id="filterType"
                  value={filterType}
                  onChange={(e) => setFilterType(e.target.value)}
                  className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                >
                  <option value="all">All Types</option>
                  {uniqueTypes.map(type => (
                    <option key={type} value={type}>{type.replace(/_/g, ' ').toUpperCase()}</option>
                  ))}
                </select>
              </div>

              <div>
                <label htmlFor="filterStatus" className="block text-sm font-medium text-gray-700 mb-1">
                  Maintenance Status
                </label>
                <select
                  id="filterStatus"
                  value={filterStatus}
                  onChange={(e) => setFilterStatus(e.target.value)}
                  className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                >
                  <option value="all">All Statuses</option>
                  <option value="overdue">Overdue</option>
                  <option value="due">Due Soon</option>
                  <option value="upcoming">Upcoming</option>
                  <option value="completed">Up to Date</option>
                </select>
              </div>

              <div>
                <label htmlFor="filterCriticality" className="block text-sm font-medium text-gray-700 mb-1">
                  Priority Level
                </label>
                <select
                  id="filterCriticality"
                  value={filterCriticality}
                  onChange={(e) => setFilterCriticality(e.target.value)}
                  className="w-full px-4 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-700"
                >
                  <option value="all">All Priorities</option>
                  <option value="critical">Critical</option>
                  <option value="high">High</option>
                  <option value="medium">Medium</option>
                  <option value="low">Low</option>
                </select>
              </div>
            </div>

            {(searchTerm || filterType !== 'all' || filterStatus !== 'all' || filterCriticality !== 'all') && (
              <div className="mt-4">
                <Button
                  variant="outline"
                  onClick={() => {
                    setSearchTerm('');
                    setFilterType('all');
                    setFilterStatus('all');
                    setFilterCriticality('all');
                  }}
                >
                  Clear All Filters
                </Button>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Equipment List */}
        {filteredEquipment.length === 0 ? (
          <Card>
            <CardContent className="p-8 text-center text-gray-600">
              <Wrench className="h-12 w-12 mx-auto mb-4 text-gray-400" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">No Equipment Found</h3>
              <p>Try adjusting your filters or search terms.</p>
            </CardContent>
          </Card>
        ) : (
          <div className="grid grid-cols-1 gap-4">
            {filteredEquipment.map(equipment => {
              const isOverdue = now ? equipment.nextMaintenanceDate < now : false;
              const daysUntilDue = now ? Math.ceil((equipment.nextMaintenanceDate.getTime() - now.getTime()) / (1000 * 60 * 60 * 24)) : 0;
              const estimatedCost = estimateCost(equipment.criticality);

              return (
                <Link key={equipment.id} href={`/equipment/${equipment.id}`}>
                  <Card className="hover:shadow-md transition-shadow cursor-pointer">
                    <CardContent className="p-6">
                      <div className="flex items-start justify-between">
                        <div className="flex-1">
                          <div className="flex items-center gap-3 mb-2">
                            <Wrench className="h-5 w-5 text-blue-700" />
                            <h3 className="text-lg font-semibold text-gray-900">{equipment.name}</h3>
                            {getStatusBadge(equipment.status)}
                            {getCriticalityBadge(equipment.criticality)}
                          </div>

                          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mt-4 text-sm">
                            <div>
                              <label className="font-medium text-gray-700">Vehicle</label>
                              <p className="text-gray-900">{equipment.vehicleName}</p>
                            </div>
                            <div>
                              <label className="font-medium text-gray-700">Type</label>
                              <p className="text-gray-900">{equipment.type.replace(/_/g, ' ').toUpperCase()}</p>
                            </div>
                            <div>
                              <label className="font-medium text-gray-700 flex items-center gap-1">
                                <MapPin className="h-3 w-3" />
                                Location
                              </label>
                              <p className="text-gray-900">{equipment.location}</p>
                            </div>
                            <div>
                              <label className="font-medium text-gray-700">Serial Number</label>
                              <p className="text-gray-900 font-mono text-xs">{equipment.serialNumber}</p>
                            </div>
                          </div>

                          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-4 pt-4 border-t border-gray-200">
                            <div className="flex items-center gap-2">
                              <Calendar className="h-4 w-4 text-gray-400" />
                              <div>
                                <label className="text-xs font-medium text-gray-600">Next Maintenance</label>
                                <p className={`text-sm font-semibold ${isOverdue ? 'text-red-600' : 'text-gray-900'}`}>
                                  {equipment.nextMaintenanceDate.toLocaleDateString()}
                                </p>
                                <p className="text-xs text-gray-600">
                                  {isOverdue ? `${Math.abs(daysUntilDue)} days overdue` : `in ${daysUntilDue} days`}
                                </p>
                              </div>
                            </div>

                            <div className="flex items-center gap-2">
                              <DollarSign className="h-4 w-4 text-gray-400" />
                              <div>
                                <label className="text-xs font-medium text-gray-600">Estimated Cost</label>
                                <p className="text-sm font-semibold text-gray-900">QAR {estimatedCost.toLocaleString()}</p>
                                <p className="text-xs text-gray-600">Per maintenance</p>
                              </div>
                            </div>

                            <div className="flex items-center gap-2">
                              <AlertTriangle className="h-4 w-4 text-gray-400" />
                              <div>
                                <label className="text-xs font-medium text-gray-600">Technician</label>
                                <p className="text-sm font-semibold text-gray-900">
                                  {equipment.assignedContacts[0]?.name || 'Unassigned'}
                                </p>
                                <p className="text-xs text-gray-600">
                                  {equipment.assignedContacts[0]?.role || ''}
                                </p>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </Link>
              );
            })}
          </div>
        )}

        <div className="mt-6 text-center text-sm text-gray-600">
          Showing {filteredEquipment.length} of {allEquipment.length} equipment items
        </div>
      </main>
    </div>
  );
}